*{
  margin: 0;
 padding: 0;
}

body{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  background-color:black;
  display: flex;
  justify-content: center;
  align-items: center;
} 
h1{
  color: white;
  text-align: center;
  margin-bottom: 1em;
}

.container{
  height: 700px;
  width: 700px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap : 1em;
}

.card{
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  transform: rotateY(180deg);
  animation: 1s hideCard linear;
  transition: transform 0.5s;  
}

@keyframes hideCard{
  0%{
    transform: rotateY(0);
  }
  100%{
    transform: rotateY(180deg);
  }
}
.card img{
  max-width: 80%;
  max-height: 80%;
  transform: scale(0);
  animation: s hideImage linear;
}

@keyframes hideImage{
  0%, 70%{
    transform: scale(1);

  }100%{
    transform: scale(0);
  }
}

.card.active{
  transform: rotateY(0);
}

.card.wrong{
  background-color: #fd254a;
}

.card.correct{
  background-color: #4caf50;
}

.card.active img{
  transform: scale(1);
}